<template>
    <span>
        <span v-for="(part, i) in text_as_array" :key="i" v-if="!is_component(part)">{{ part }}</span>
        <router-link :to="`/components/${part}`" v-else>{{ part }}</router-link>
    </span>
</template>

<script>
export default {
    name: "link-text",
    props: {
        
    },
    data() {
        return {
            cached_text: null
        }
    },
    computed: {
        text: {
            get() {
                if(this.cached_text) return this.cached_text
                return (this.$slots.default[0].text + "").trim();
            },
            set(val) {
                this.cached_text = val;
            }
        },
        component_names() {
            let arr = [];
            this.$store.state.components.forEach(c => arr.push(c.name));
            return arr;
        },
        included() {
            let included = [];
            this.component_names.forEach(cn => {
                if(this.text.includes(cn)) included.push(cn);
            });
            return included;
        },
        text_as_array() {
            console.log("Eval descr.!")
            if(this.included.length == 0) return [this.text];
            return this.text.replace(new RegExp(`${this.included.join("|")}`, "g"), (match) => {
                return `&docu.split-point;${match}&docu.split-point;`
            }).split("&docu.split-point;");
        }
    },
    methods: {
        is_component(str) {
            return this.component_names.includes(str);
        }
    },
    watch: {
        "$route" (to, from) {
            this.text = (this.$slots.default[0].text + "").trim()
        }
    }
}
</script>
