import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    components: [],
    component_search: ""
  },
  mutations: {
    setData(state, payload) {
      if(Array.isArray(payload.data) && payload.data[0].name) {
        Vue.set(state, payload.key, payload.data.sort((a, b) => {
          if(a.name > b.name) return 1;
          if(b.name > a.name) return -1;
          return 0;
        }));
      } else {
        Vue.set(state, payload.key, payload.data);
      }
    }
  },
  actions: {

  }
})
